import React from "react"

const Combinations = ({ availableCombos, onHideCombinations }) => {
  return (
    <Div position='relative' display='block' bg='white'>
      <H4 mt={0}width={1} display='block' py={3} textAlign='center'>{availableCombos.length} Accessible Combinations</H4>
      <Flex width={1} flexWrap='wrap'>
      {availableCombos.map((combo, i) => {
        const currentCombination = {
          bg: combo[0],
          color: combo[1],
          borderColor: combo[1]
        }
        return (
          <Div
            width={[1, 1 / 2, 1 / 4]}
            key={i}
            css={`
              position: relative;
            `}
          >
            <MiniTextBlock
              key={i}
              currentCombination={currentCombination}
            />
          </Div>
        )
      })}
    </Flex>
            <ButtonIcon
              onClick={onHideCombinations}
              icon='remove'
              css={`
                position: absolute;
                top: 16px;
                right: 16px;
                backface-visibility: hidden;
              `}
            />
            <ButtonIcon
              onClick={onHideCombinations}
              icon='left'
              css={`
                position: absolute;
                top: 16px;
                left: 16px;
                backface-visibility: hidden;
              `}
            />
              
    </Div>
  )
}

export default Combinations
